.c-loginForm {
  font-family: "Poppins", sans-serif;
  color: skyblue;
}
/* ------------------------------------------------------------------ */

/* DESIGN INPUT FIELDS */
.c-loginForm .form-control
/* .c-loginForm input:-internal-autofill-selected  */ {
  color: aqua;
  background-color: rgb(43, 40, 40);
}

.c-loginForm .form-control:focus {
  background-color: rgb(43, 40, 40);
  border-color: aqua;
  color: aqua;
}

.c-loginForm .form-control.is-invalid,
.c-loginForm.was-validated .form-control:invalid,
.c-loginForm .form-control.is-invalid:focus,
.c-loginForm.was-validated .form-control:invalid:focus {
  border-color: tomato;
}

.c-loginForm .form-control.is-valid,
.c-loginForm.was-validated .form-control:valid,
.c-loginForm .form-control.is-valid:focus,
.c-loginForm.was-validated .form-control:valid:focus {
  border-color: aquamarine;
}
/* ------------------------------------------------------------------ */

/* DESIGN FEEDBACK */
.c-loginForm .invalid-feedback {
  color: tomato;
}

.c-loginForm .valid-feedback {
  color: aquamarine;
}
/* ------------------------------------------------------------------ */

/* DESIGN SUBMIT BUTTON */
.c-loginForm .submitForm {
  -webkit-align-items: baseline;
          align-items: baseline;
}

.c-loginForm .submitForm > button {
