.c-typemeNavBarWrap {
  border-bottom: 1px solid pink;
}

/* MAIN FIXED NAVABAR */
.c-typemeNavBarWrap .fixedMenu {
  padding-bottom: 0px;
  z-index: 2;
}
/* --------------------------------------------------------------------------- */

/* FLOATING NAVABAR */
.c-typemeNavBarWrap .floatMenu {
  position: absolute;
  display: none;
  right: 16px;
  top: 49px;
  z-index: 1;
  border: 1px solid pink;
  border-top: 1px solid pink;
  box-shadow: 0px 3px 10px 3px rgba(180, 86, 158, 0.47);
}

/* FLOATING NAVBAR SWITCHES */
.c-typemeNavBarWrap .floatMenu .form-switch > .form-check-label {
  padding-right: 100px;
}

.c-typemeNavBarWrap .floatMenu .form-switch {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 0;
}

.c-typemeNavBarWrap .floatMenu .form-switch .form-check-input {
  position: unset;
  float: none;
}
/* --------------------------------------------------------------------------- */

/* NAVBAR NAVIGATION TABS */
.c-typemeNavBarWrap .fixedMenu .nav-tabs .nav-link:focus,
.c-typemeNavBarWrap .fixedMenu .nav-tabs .nav-link:active,
.c-typemeNavBarWrap .fixedMenu .nav-tabs .nav-item.show .nav-link,
.c-typemeNavBarWrap .fixedMenu .nav-tabs .nav-link.active {
  border-color: pink;
  border-bottom: 1px solid #343a40;
}

.c-typemeNavBarWrap .fixedMenu .nav-tabs .nav-link:not(.active):hover,
.c-typemeNavBarWrap .fixedMenu .nav-tabs .nav-link:not(.active) {
  border: 1px solid transparent;
}
/* --------------------------------------------------------------------------- */

/* NAVIGATION TABS COMMON */

.c-typemeNavBarWrap .nav-tabs {
  border-bottom: none;
}

.c-typemeNavBarWrap .nav-tabs .nav-item.show .nav-link,
.c-typemeNavBarWrap .nav-tabs .nav-link.active {
  /* color: #495057; */
  background-color: transparent;
  background-color: initial;
}
/* --------------------------------------------------------------------------- */

/* NAVIGATION TABS ERROR */
.c-typemeNavBarWrap .blockOnError.hide {
  display: none;
}

.c-typemeNavBarWrap .blockOnError .nav-item {
  margin: 0 7px;
  font-size: 1.3em;
  padding-bottom: 5px;
}

.c-typemeNavBarWrap .blockOnError.nav-tabs .nav-link:not(.active) {
  border: 1px solid transparent;
}

.c-typemeNavBarWrap .blockOnError.nav-tabs .nav-link:not(.active):hover {
  border-bottom: 1px solid pink;
}

.c-typemeNavBarWrap .blockOnError.nav-tabs .nav-link:focus,
.c-typemeNavBarWrap .blockOnError.nav-tabs .nav-link:active,
.c-typemeNavBarWrap .blockOnError.nav-tabs .nav-item.show .nav-link,
.c-typemeNavBarWrap .blockOnError.nav-tabs .nav-link.active {
  border: 1px solid #343a40;
  border-bottom: 1px solid pink;
}
/* --------------------------------------------------------------------------- */

.c-typemeNavBarWrap .floatMenu.settings,
.c-typemeNavBarWrap .floatMenu.sound {
  display: block;
}

.letterSpan,
.errorChar {
  border: 2px solid black;
  border-radius: 5px;
  width: 32px;
  min-width: 32px;
  line-height: 1.5em;
  margin: 2px -8.5px;
  font-size: 2em;
  text-align: center;
  display: inline-block;
  white-space: pre;
  font-family: "Ubuntu Mono", monospace, "Grandstander";
}

.wordsSpan {
  white-space: nowrap;
}

.wordsSpan .letterSpan._blocked {
  background-color: rgb(35, 47, 56);
  color: aliceblue;
  opacity: 0.7;
}

.wordsSpan .letterSpan._correct {
  background-color: lightgreen;
  opacity: 0.7;
  color: black;
}

.wordsSpan .letterSpan._error {
  background-color: lightpink;
  opacity: 0.7;
  color: black;
}

.wordsSpan .letterSpan._fixed {
  background-color: gold;
  opacity: 0.7;
  color: black;
}

.wordsSpan .letterSpan._clean {
  background-color: rgb(35, 47, 56);
  color: aliceblue;
  opacity: 0.7;
}

.wordsSpan .errorChar {
  position: relative;
  right: 15px;
  opacity: 0;
  display: hidden;
}

.wordsSpan .errorChar.show1 {
  -webkit-animation: displayError1 1.3s;
          animation: displayError1 1.3s;
}

.wordsSpan .errorChar.show2 {
  -webkit-animation: displayError2 1.3s;
          animation: displayError2 1.3s;
}

@-webkit-keyframes displayError1 {
  from {
    background-color: tomato;
    color: white;
    opacity: 1;
  }
  to {
    background-color: rgb(35, 47, 56);
    color: aliceblue;
    opacity: 0;
  }
}

@keyframes displayError1 {
  from {
    background-color: tomato;
    color: white;
    opacity: 1;
  }
  to {
    background-color: rgb(35, 47, 56);
    color: aliceblue;
    opacity: 0;
  }
}

@-webkit-keyframes displayError2 {
  from {
    background-color: tomato;
    color: white;
    opacity: 1;
  }
  to {
    background-color: rgb(35, 47, 56);
    color: aliceblue;
    opacity: 0;
  }
}

@keyframes displayError2 {
  from {
    background-color: tomato;
    color: white;
    opacity: 1;
  }
  to {
    background-color: rgb(35, 47, 56);
    color: aliceblue;
    opacity: 0;
  }
}

